//路由配置
import App from '../App'
const home = r => require.ensure([], () => r(require('../page/home/home')), 'home');
const login = r=>require.ensure([],() => r(require('../page/login/login')),'login');
const city = r =>require.ensure([],() => r(require('../page/city/city.vue'),'city'));
const msite =r =>require.ensure([],() => r(require('../page/msite/msite.vue')),'msite');
const food =r=>require.ensure([],() => r(require('../page/food/food.vue')),'food');
const shop =r=>require.ensure([],()=>r(require('../page/shop/shop.vue')),'shop');
const shopSafe=r=>require.ensure([],()=>r(require('../page/shop/childer/childer/shopSafe.vue')));
const shopDetail =r=>require.ensure([],()=>r(require('../page/shop/childer/shopDetail.vue')),'shopDetail');
const foodDetail =r=>require.ensure([],()=>r(require('../page/shop/childer/foodDetail.vue')),'foodDetail');
const confirmOrder =r=>require.ensure([],()=>r(require('../page/confirmOrder/confirmOrder.vue')),'confirmOrder');
const chooseAddress=r=>require.ensure([],()=>r(require('../page/confirmOrder/children/chooseAddress.vue')),'chooseAddress');
const addAddress=r=>require.ensure([],()=>r(require('../page/confirmOrder/children/children/addAddress.vue')),'addAddress');
const remark=r=>require.ensure([],()=>r(require('../page/confirmOrder/children/remark.vue')),'remark');
const searchAddress=r=>require.ensure([],()=>r(require('../page/confirmOrder/children/children/chiildren/searchAddress.vue')),'searchAddress');
const invoice=r=>require.ensure([],()=>r(require('../page/confirmOrder/children/invoice.vue')),'invoice');
const payment=r=>require.ensure([],()=>r(require('../page/confirmOrder/children/payment.vue')),'payment');
const order=r=>require.ensure([],()=>r(require('../page/order/order.vue')),'order');
const orderDetail=r=>require.ensure([],()=>r(require('../page/order/childer/orderDetail.vue')),'orderDetail');
const profile=r=>require.ensure([],()=>r(require('../page/profile/profile.vue')),'profile');
const info=r=>require.ensure([],()=>r(require('../page/profile/childer/info.vue')),'info');
const setusername=r=>require.ensure([],()=>r(require('../page/profile/childer/setusername.vue')),'setusername');
const address=r=>require.ensure([],()=>r(require('../page/profile/childer/childer/address.vue')),'address');
const add=r=>require.ensure([],()=>r(require('../page/profile/childer/childer/childer/add.vue')),'add');
const addDetail=r=>require.ensure([],()=>r(require('../page/profile/childer/childer/childer/childer/addDetail.vue')),'addDetail');
const forget=r=>require.ensure([],()=>r(require('../page/forget/forget.vue')),'forget');
const balance=r=>require.ensure([],()=>r(require('../page/balance/balance.vue')),'balance');
const detail=r=>require.ensure([],()=>(require('../page/balance/children/detail.vue')),'detail');
const benefit=r=>require.ensure([],()=>(require('../page/benefit/benefit.vue')),'benefit');
const hbHistory=r=>require.ensure([],()=>(require('../page/benefit/childern/hbHistory.vue')),'hbHistory');
const hbDescription=r=>require.ensure([],()=>(require('../page/benefit/childern/hbDescription.vue')),'hbDescription');
const exchange=r=>require.ensure([],()=>(require('../page/benefit/childern/exchange.vue')),'exchange');
const commend=r=>require.ensure([],()=>(require('../page/benefit/childern/commend.vue')),'commend');
const coupon=r=>require.ensure([],()=>(require('../page/benefit/childern/coupon.vue')),'coupon');
const download=r=>require.ensure([],()=>(require('../page/download/download.vue')),'download');
const points=r=>require.ensure([],()=>(require('../page/points/points.vue')),'points');
const details=r=>require.ensure([],()=>(require('../page/points/children/detail.vue')),'detail');
const vipcard=r=>require.ensure([],()=>(require('../page/vipcard/vipcard.vue')),'vipcard');
const vipDescription=r=>require.ensure([],()=>(require('../page/vipcard/children/vipDescription.vue')),'vipDescription');
const useCart=r=>require.ensure([],()=>(require('../page/vipcard/children/useCart.vue')),'useCart');
const invoiceRecord=r=>require.ensure([],()=>(require('../page/vipcard/children/invoiceRecord.vue')),'invoiceRecord');
const service=r=>require.ensure([],()=>(require('../page/service/service.vue')),'service');
const questionDetail=r=>require.ensure([],()=>(require('../page/service/children/questionDetail.vue')),'questionDetail');
const search=r=>require.ensure([],()=>(require('../page/search/search.vue')),'search');
export default [{
    path: '/',
    component: App,//顶臣路由
    children: [
        {//二级路由
        //地址为空时转跳到home页面
        path: '',
        redirect: '/home' //重新定义url转跳
        },
        //城市列表页
        {
            path: '/home',
            component: home
        },
        //注册页
        {
            path:'/login',
            component: login
        },
        //选择城市页
        {
            path:'/city/:cityid',
            component:city
        },
        //所有商品列表
        {
            path:'/msite',
            component:msite
        },
        //特色商品列表
        {
            path:'/food',
            component:food
        },
        //商品详情页
        {
            path:'/shop',
            component:shop,
            children:[{
                path:'shopDetail',//商品详情页
                component:shopDetail,
                children:[{
                    path: 'shopSafe', //商铺安全认证页
                    component: shopSafe,
                }]

            },{
                path:'foodDetail',//商品页
                component:foodDetail
            }]
        },
        //确认订单页
        {
            path:'/confirmOrder',
            component:confirmOrder,
            children:[{
                path:'chooseAddress',
                component:chooseAddress,
                children:[{
                    path:'addAddress',
                    component:addAddress,
                    children:[{
                        path:'searchAddress',
                        component:searchAddress
                    }]
                }]
            },{
                path:'remark',
                component:remark
            },{
                path:'invoice',
                component:invoice
            },{
                path:'payment',
                component:payment
            }]
        },
        //订单页列表
        {
            path:'/order',
            component:order,
            children:[{
                path:'orderDetail',//显示订单详情页
                component:orderDetail
            }]
        },
        //用户
        {
            path:'/profile',
            component:profile,
            children:[{
                path:'info',
                component:info,
                children:[
                    {
                        path:'address',
                        component:address,
                        children:[
                            {
                                path:'add',
                                component:add,
                                children:[
                                    {
                                        path:'addDetail',
                                        component:addDetail
                                    }
                                ]
                            }
                        ]
                    }
                ]
            },{
                path:'setusername',
                component:setusername
            }]
        },
        //修改密码
        {
            path:'forget',
            component:forget
        },
        //余额
        {
            path:'/balance',
            component:balance,
            children:[{
                path:'detail',
                component:detail
            }]
        },
        //优惠卷
        {
            path:'/benefit',
            component:benefit,
            children:[{
                path:'hbHistory',
                component:hbHistory
            },{
                path:'hbDescription',
                component:hbDescription
            },{
                path:'exchange',
                component:exchange
            },{
                path:'commend',
                component:commend
            },{
                path:'coupon',
                component:coupon
            }]
        },
        //下载页
        {
            path:'download',
            component:download
        },
        //我的积分
        {
            path:'points',
            component:points,
            children:[{
                path:'detail',
                component:details
            }]
        },
        //会员卡
        {
            path:'vipcard',
            component:vipcard,
            children:[{
                path:'vipDescription',
                component:vipDescription
            },{
                path:'useCart',
                component:useCart
            },{
                path:'invoiceRecord',
                component:invoiceRecord
            }]
        },
        //服务中心
        {
            path:'/service',
            component:service,
            children:[
                {
                    path:'questionDetail',
                    component:questionDetail
                }
            ]
        },
        //搜索页
        {
            path:'/search/geohash',
            component:search
        }
    ]
}]


