(function(doc,win){
    var docem=doc.documentElement;//获取全局DOM
    //orientationchange水平或垂直翻转触发resize浏览器窗口发生变化时触发
    //兼容看window有没有orientationchange事件没有的话就resize
    var resize='orientationchange' in window ? 'orientationchange' : 'resize';
    var recalc=function(){
        var clientwidth=docem.clientWidth;//获取窗口宽度
        //判断是否有宽没有就返回
        if(!clientwidth) return;
        //根据浏览器大小变化设置全体字体大小
        docem.style.fontSize=20*(clientwidth/320)+'px';
    }
    if(!doc.addEventListener) return;
    //设置传入事件
    //设置窗口大小变化事件
    win.addEventListener(resize,recalc,false);
    //加载触发字体适应事件
    //DOMContentLoaded DOM树完成后直接触发该方法load是页面全部加载完后触发
    doc.addEventListener('DOMContentLoaded',recalc,false);
})(document,window);


