import fetch from '../config/fetch'
import {getStore} from '../config/mUtils'
//获取首页默认地址
export const cityGuess=() =>fetch('/v1/cities',{
    type: 'guess',
});

//获取用户信息
export const getUser=() =>fetch('/v1/user',{uer_id:getStore('uer_id')});

//获取首页所以城市
export const groupcity=() =>fetch('/v1/cities',{
    type:'group'
});

//获取热门城市
export const hotcity=() =>fetch('/v1/cities',{
    type:'hot'
});

//获取验证码
export const getcaptchas=() =>fetch('/v1/captchas',{},'POST');

//账号密码登陆
export const accountLogin=(username,password,captcha_code) =>fetch('/v2/login',{username,password,captcha_code},'POST')

//获取当前城市
export const currentcity=number =>fetch('/v1/cities/'+number);

//获取搜索地址
export const searchplace = (cityid, value) => fetch('/v1/pois', {
	type: 'search',
	city_id: cityid,
	keyword: value
});

//获取食品分类列表
export const msiteFoodTypes=geohash=>fetch('/v2/index_entry',{
    geohash,
    group_type:1,
    'flags[]':'F'
})

//获取选择地的位置信息
export const msiteAddress=geohash=>fetch('/v2/pois/'+geohash)

//获取misit店铺数据批次加载
export const shopList=(latitude,longitude,offset,restaurant_category_id='',restaurant_category_ids='',order_by='',delivery_mode='',support_ids=[])=>{
    let supportStr='';
    support_ids.forEach((item)=>{
        if(item.status)
        {
            supportStr+='support_ids[]'+item.id;
        }
    });
    let data={
        latitude,//当前经度
        longitude,//当前维度
        offset,//批次加载一次加载20个
        limit:'20',//每次加载入宫offset==0就加载20如果不等于0就offset/20*20获得的值再sql语句中去掉前面多少笔记录再返回20条
        'extrasx[]':'activities',
        keyword:'',
        restaurant_category_id,
        'restaurant_category_ids[]':restaurant_category_ids,
        order_by,
        'delivery_mode[]':delivery_mode+supportStr
    };
    return fetch('/shopping/restaurants',data);
}

//获取food页面左侧种类数据
export const foodCategory=(latitude,longitude)=>fetch('/shopping/v2/restaurant/category',{
    latitude,
    longitude
})

//获取food页面的配送方式
export const foodDelivery=(latitude,longitude)=>fetch('/shopping/v1/restaurants/delivery_modes',{
    latitude,
    longitude,
    kw:''
})

//商家属性活动列表
export const foodActivity=(latitude,longitude)=>fetch('/shopping/v1/restaurants/activity_attributes',{
    latitude,
    longitude,
    kw:''
})

//shop页面详情
export const shopDetails=(shopid,latitude,longitude)=>fetch('/shopping/restaurant/'+shopid,{
    latitude,
    longitude:longitude+'&extras[]=activities&extras[]=album&extras[]=license&extras[]=identification&extras[]=statistics'
})

// 获取shop页面菜单列表
export const foodMenu=restaurant_id=>fetch('/shopping/v2/menu',{
    restaurant_id
})

// 获取商品评价列表
export const getRatingList=(shopid,off_set,tag_name="")=>fetch('/ugc/v2/restaurants/' + shopid + '/ratings',{
    has_content:true,
    off_set,
    limit:10,
    tag_name
})

//获取商铺评价分数
export const ratingScores=shopid=>fetch('/ugc/v2/restaurants/'+shopid+ '/ratings/scores');


// 获取商铺评价分类
export const ratingTags=shopid=>fetch('/ugc/v2/restaurants/' + shopid + '/ratings/tags');

//确定订单
export const checkout=(geohash,entities,shopid)=>fetch('/v1/carts/checkout',{
    come_from:'web',
    geohash,
    entities,
    restaurant_id:shopid,
},'POST');

//获取地址列表
export const getAddress=(id,sig)=>fetch('/v1/carts/' + id + '/addresses',{
    sig
});

//下订单
export const placeOrders=(user_id,cart_id,address_id,description,entities,geohash,sig)=>fetch('/v1/users/' + user_id + '/carts/' + cart_id + '/orders',{
    address_id,
    come_from:'mobile_web',
    deliver_time:'',
    description,
    entities,
    geohash,
    paymethod_id:1,
    sig
},'POST')

//个人中心编辑地址
export const getAddressList=(user_id)=>fetch('/v1/users/'+user_id+'/addresses');

//删除收货地址
export const deleteAddress=(user_id,addressid)=>fetch('/v1/users/' + user_id + '/addresses/' + addressid, {}, 'DELETE')

//添加收货地址
export const postAddAddress = (userId, address, address_detail, geohash, name, phone, phone_bk, poi_type, sex, tag, tag_type) => fetch('/v1/users/' + userId + '/addresses', {
	address,
	address_detail,
	geohash,
	name,
	phone,
	phone_bk,
	poi_type,
	sex,
	tag,
	tag_type,
}, 'POST');

//搜索地址
export const searchNearby = keyword => fetch('/v1/pois', {
	type: 'nearby',
	keyword
});

//获取快速备注列表
export const getRemark=(id,sig)=>fetch('/v1/carts/' + id + '/remarks',{
    sig
})


/**
 * 重新发送订单验证码
 */

export const payRequest = (merchantOrderNo, userId) => fetch('/payapi/payment/queryOrder', {
	merchantId: 5,
	merchantOrderNo,
	source: 'MOBILE_WAP',
	userId,
	version: '1.0.0',
});
//获取用户订单集合信息
export const getOrderList=(user_id,offset)=>fetch('/bos/v2/users/' + user_id + '/orders',{
    limit: 10,
	offset,
})
//获取单个订单详情
export const getOrderDetail=(user_id,orderid)=>fetch('/bos/v1/users/' + user_id + '/orders/' + orderid + '/snapshot');

//推出登陆
export const signout=()=>fetch('/v2/signout')

//获取短信验证码
export const mobileCode=phone=>fetch('/v4/mobile/verify_code/send',{
    mobile: phone,
	scene: 'login',
	type: 'sms'
},'POST')

//检查当前账号是否纯在
export const checkExsis=(checkNumer,type)=>fetch('/v1/users/exists',{
    [type]: checkNumber,
	type 
})

//发送账号
export const sendMobile=(sendData,captcha_code,type,password)=>fetch('/v1/mobile/verify_code/send',{
    action: "send",
	captcha_code,
	[type]: sendData,
	type: "sms",
	way: type,
	password,
})

//修改密码
export const changePassword=(username,oldpassWord,newpassword,confirmpassword,captcha_code)=>fetch('/v2/changepassword',{
    username,
    oldpassWord,
    newpassword,
    confirmpassword,
    captcha_code
},'POST')

//获取红包
export const getHongbaoNum=id=>fetch('/promotion/v2/users/' + id + '/expired_hongbaos?limit=20&offset=0');

//获取历史红包
export const getExpired=id=>fetch('/promotion/v2/users/' + id + '/expired_hongbaos?limit=20&offset=0');

//兑换红包
export const exChangeHongbao=(id,exchange_code,captcha_code)=>fetch('/v1/users/' + id + '/hongbao/exchange',{
exchange_code,
captcha_code
},'POST')

//兑换会员
export const vipCart=(id,name,password)=>fetch('/member/v1/users/' + id + '/delivery_card/physical_card/bind',{
    name,
    password
},'POST')

//获取服务中心信息
export const getService=()=>fetch('/v3/profile/explain')

//获取search页面搜索结果
export const searchRestaurant=(geohash,keyword)=>fetch('/v4/restaurants',{
    'extras[]': 'restaurant_activity',
	geohash,
	keyword,
	type: 'search'
})



