import {
    baseUrl
} from './env'
import { request } from 'https';
//加上async该方法为异步
export default async (url = '', data = {}, type = 'GET', method = 'fetch') => {  
    type = type.toUpperCase();//将英文字母转换为大写
    url = baseUrl + url;
    //GET请求拼接请求字符
    if (type == 'GET') {
        console.log(data);
        let dataStr = '';//字符串拼接符
        Object.keys(data).forEach(key => {
            dataStr += key + '=' + data[key] + '&';
        })
        if (dataStr !== '') {
            dataStr = dataStr.substr(0, dataStr.lastIndexOf('&'));
            url = url + '?' + dataStr;
        }
        console.log(url);
    }
    //判断是否支持fentch
    if (window.fetch && method == 'fetch') {
        //fetch请求参数配置
        let requesConfig = {
            credentials: 'include',//在当地域名自动发送cookie
            method: type,//请求类型
            headers: {//配置请求头信息
                'Accept': 'application/json',//需要接受的数据类型
                'Content-Type': 'application/json'//发生的数据类型
            },
            mode: 'cors',//是否允许跨域cors允许
            cache: 'force-cache'//表示fetch请求不顾一切的依赖缓存没有缓存后再发送请求
        }
        //如果是post请求添加参数
        if (type == 'POST') {
            //往对象里面添加数据
            Object.defineProperty(requesConfig, 'body', {
                value: JSON.stringify(data)
            })
        }
        try {
            //await只能在async中使用解决回调地狱await要等待该方法完成后再执行虽然是异步但加了async就保证该方法一步一步执行
            const response = await fetch(url, requesConfig);
            //转换为JSON
            const responseJson = await response.json();

            return responseJson;
        } catch (error) {
            throw new Error(error)
        }
    } else {
        return new Promise((resolve, reject) => {
            //不支持fetch的话
            let requestobj;
            if (window.XMLHttpRequest) {
                requestobj = new XMLHttpRequest();
            } else {
                //IE老版本
                requestobj = new ActiveXObject;
            }
            let sendData = '';
            if (type == 'POST') {
                sendData = JSON.stringify(data);
            }
            requestobj.open(type,url,true);//添加请求类型和url
            requestobj.setRequestHeader('Content-Type','application/x-www-form-urlencoded');//添加请求头
            //发起请求
            requestobj.onreadystatechange =()=>{
                //请求完成
                if(requestobj.readyState==4)
                {
                    //请求正确
                    if(requestobj.status==200)
                    {
                        let obj=requestobj.response;
                        if(typeof obj !=='object')
                        {
                            obj=JSON.parse(obj);
                        }
                        //返回一个解析过带着给定值的Promise对象
                        resovle(obj);
                    }else{
                        reject(requestobj);
                    }
                }
            }
        })

    }
}


