// 配置编译环境和上线环境
// baseUrl:服务器地址
// routerModel:路由模式
// imageBaseUrl:图片所在的服务器
let baseUrl='';
let routerModel='hash';
let imageBaseUrl='';
//检查时上线环境还是编译环境
if(process.env.NODE_ENV=='development')
{
    //上线环境
    imageBaseUrl = 'http://cangdu.org:8001/img/';
    baseUrl = 'http://cangdu.org:8001';
}else{
    //生产环境
    baseUrl = 'http://cangdu.org:8001';
    imageBaseUrl = 'http://cangdu.org:8001/img/';
}
//返回变量
export {
    baseUrl,
    routerModel,
    imageBaseUrl
}



