import {
    GET_USERINFO,
    RECORD_USERINFO,
    SAVE_GEOHASH,
    RECORD_ADDRESS,
    ADD_CART,
    REDUCE_CART,
    INIT_BUYCART,
    CLEAR_CART,
    RECORD_SHOPDETAIL,
    CHOOSE_ADDRESS,
    NEED_VALIDATION,
    SAVE_ORDER_PARAM,
    ORDER_SUCCESS,
    SAVE_SHOPID,
    SAVE_CART_ID_SIG,
    CONFIRM_ADDRESS,
    CHOOSE_SEARCH_ADDRESS,
    CONFIRM_REMARK,
    CONFIRM_INVOICE,
    CHANGE_ORDER_PARAM,
    SAVE_ORDER,
    SAVE_AVANDER,
    OUT_LOGIN,
    RETSET_NAME,
    SAVE_ADDRESS,
    ADD_ADDRESS,
    SAVE_ADDDETAIL,
    BUY_CART,
    SAVE_QUESTION
} from './mutation-types'
import {setStore, getStore} from '../config/mUtils'
//用户信息存入vuex
export default {
    //取用户信息
    [GET_USERINFO](state,info) {
       
        if(state.userInfo && (state.userInfo.username!==info.username))
        {
            return;
        }
        if(!state.login)
        {
            return;
        }
        if(!info.message)
        {
            state.userInfo={...info};
        }else{
            state.userInfo=null;
        }
    },
    //记录用户信息
    [RECORD_USERINFO](state,info)
    {
        state.userInfo=info;
        state.login=true;
       
        setStore('user_id',info.user_id);
    },
    //记录地址位置
    [SAVE_GEOHASH](state,info)
    {
        state.geohash=info;
    },
    //记录经纬度
    [RECORD_ADDRESS](state,{
        latitude,
		longitude
    }){
        state.latitude=latitude;
        state.longitude=longitude;
    },
    //购物车信息
    [ADD_CART](state,{
        shopid,
        category_id,
        item_id,
        food_id,
        name,
        price,
        specs,
        packing_fee,
        sku_id,
        stock
    }){
        let cart=state.cartList;
        let shop=cart[shopid]=(cart[shopid] || {});
        let category=shop[category_id]=(shop[category_id] || {});
        let item=category[item_id]=(category[item_id] || {});
        if(item[food_id])
        {
            item[food_id]['num']++;
        }else{
            item[food_id]={
                "num":1,
                "id":food_id,
                "name":name,
                "price":price,
                "specs":specs,
                "packing_fee":packing_fee,
                "sku_id":sku_id,
                "stock":stock
            };
        }
        state.cartList={...cart};
        setStore('buyCart',state.cartList);
    },
    //移出购物车
    [REDUCE_CART](state,{
        shopid,
        category_id,
        item_id,
        food_id,
        name,
        price,
        specs
    }){
        let cart=state.cartList;
        let shop=(cart[shopid] || {});
        let category=(shop[category_id] || {});
        let item=(category[item_id] || {});
       if(item[food_id] && item)
        {
            if(item[food_id]['num']>0)
            {
                item[food_id]['num']--;
                state.cartList={...cart};
                //存入loadStr
                setStore('buyCart',state.cartList);
            }else{
                //商品为0则清空当前信息
                item[food_id]=nulll;
            }
        }
    },
    //页面初始化时从本地缓存获取购物车信息
    [INIT_BUYCART](state){
        let initCart=getStore('buyCart');
        if(initCart)
        {
            state.cartList=JSON.parse(initCart);
        }
    },
    //清空当前购物车信息
    [CLEAR_CART](state,shopid){
        state.cartList[shopid]=null;
        state.cartList={...state.cartList};
        setStore('buyCart',state.cartList);
    },
    //获取商家详细信息
    [RECORD_SHOPDETAIL](state,detail)
    {
        state.shopDetail=detail;
    },
    //选中的地址
    [CHOOSE_ADDRESS](state,{
        addres,
        index
    }){

        state.choosedAddress=addres;
        state.addressIndex=index;
    },
    //保存下单时需要返回的验证值
    [NEED_VALIDATION](state,needValidation){
        state.needValidation=needValidation;
    },
    //保存下单后的id,sig
    [SAVE_CART_ID_SIG](state,{
        cart_id,
        sig
    }){
        state.cartId=cart_id;
        state.sig=sig;
    },
    //保存下单参数用户页面调用
    [SAVE_ORDER_PARAM](state,newParam){
        state.orderParam = newParam;
        alert(JSON.stringify(state.orderParam));
    },
    //修改下单参数
    [CHANGE_ORDER_PARAM](state,newParam)
    {
        //特点：浅拷贝、对象属性的合并第一个参数时转换的类型后面两个时变量传两个合并再转换一个直接转换
        state.orderParam=Object.assign({},state.orderParam,newParam);
    },
    //下单成功后保存订单返回的信息
    [ORDER_SUCCESS](state,order)
    {
        state.cartPrice=null,
        state.orderMessage=order;
    },
    //保存商铺id
    [SAVE_SHOPID](state,shopid)
    {
        state.shopId=shopid;
    },
    //确认添加新的地址每次添加新地址的后变量都会变这样可以监控该值一旦发生变化就取服务器取新的数据
    [CONFIRM_ADDRESS](state,newAdderss1)
    {
        state.newAddress.push(newAdderss1);
    },
    //选择搜索的地址
    [CHOOSE_SEARCH_ADDRESS](state,info)
    {
        state.searchAddress=info;
    },
    //记录订单页用户选择的备注
    [CONFIRM_REMARK](state,{
        remarkText,
        inputText
    }){
        state.remarkText=remarkText;
        state.inputText=inputText;
    },
    //是否需要发票
    [CONFIRM_INVOICE](state,invoice){
        state.invoice=invoice;
    },
    //进入订单详情页时保存订单信息
    [SAVE_ORDER](state,orderDateil)
    {
        state.orderDateil=orderDateil;
    },
    //保存用户头像地址
    [SAVE_AVANDER](state,imgPath)
    {
        state.imgPath=imgPath;
    },
    [OUT_LOGIN](state){
        state.userInfo={};
        state.login=false;
    },
    //修改用户名
    [RETSET_NAME](state,username)
    {
        //替换原先的再合并再一个新的对象
        state.userInfo=Object.assign({},state.userInfo,{username});
    },
    //删除地址列表
    [SAVE_ADDRESS](state,newaddress)
    {
        state.removeAddress=newaddress;
    },
    //增加地址
    [ADD_ADDRESS](state,obj)
    {
        //合并数组
        state.removeAddress=[obj,...state.removeAddress]
    },
    //增加地址name
    [SAVE_ADDDETAIL](state,addAddress)
    {
        state.addAddress=addAddress;
    },
    //记录会员卡价格
    [BUY_CART](state,price)
    {
        state.cartPrice=price;
    },
    //所选问题详情保存
    [SAVE_QUESTION](state,{
        item,
        tx
    })
    {
        state.title=item;
       state.tx=tx;
    }
}


