import Vuex from 'vuex'
import Vue from 'vue'
import mutations from  './mutations'
import actions from './action'
import getters from './'
Vue.use(Vuex)
const state={
    userInfo:null,//用户信息
    login:true,
    geohash:'31.22299,121.36025',//地理的geohash值
    latitude:'',//当前经度
    longitude:'',//当前维度
    cartList:{},//加入购物车商品的列表
    shopDetail:null,//商家详细信息,
    remarkText:null,//可选备注内容
    inputText:'',//输入备注内容
    invoice:false,//开发票
    choosedAddress:null,//选中地址
    addressIndex:null,//选中地址的索引值
    needValidation:null,//确定订单是否验证
    sig:null,//购物车的sig
    cartId:null,//购物车id
    orderParam:null,//订单的参数
    cartPrice:null,//会员卡的价格
    orderMessage:null,//订单返回的信息
    shopId:null,//商铺id
    searchAddress:null,//搜索并选择地址
    newAddress:[],//确认订单页新地址
    orderDateil:null,//订单详情页信息
    imgPath:null,//用户头像地址
    removeAddress:{},//移除地址
    addAddress:'',//新增地址
    title:null,//问题标题
    tx:null,//问题详情
} 
export default new Vuex.Store({
    state,
    mutations,
    actions
})


