<template>
  <div>
				<!--v-if="$router.meta.keepAlive"
         那个页面路由的keepAlive设置成true就会缓存当回到这个页面时会判断当前页面keepAlive是否true
				的话v-if去掉重新加载的直接使用缓存 -->
    <transition name="router-fade" mode="out-in">
      <!-- transition进入页面时加载动画 -->
      <keep-alive>
        <!-- keep-alive组件切换过程中将状态保留再内存中防止DOM再次渲染 -->
        <!-- 当keepAlive为true的时候将页面缓存等再进入该页面的时候直接加载缓存不重新获取 -->
        <router-view v-if="$route.meta.keepAlive"></router-view>
      </keep-alive>
    </transition>
    <transition name="router-fade" mode="out-in">
      <!-- 当keepAlive为false的时候直接加载新的不使用缓存 -->
      <router-view v-if="!$route.meta.keepAlive"></router-view>
    </transition>
    <!-- 加载svg图 -->
    <svg-icon></svg-icon>	
  </div>
</template>

<script>
import svgIcon from './components/common/svg'
export default {
  name: 'App',
  components: {
    svgIcon
  }
}
</script>
<style lang="scss">
  @import './style/common.scss';
  //路由切换动画
  .router-fade-enter-active,.router-fade-leave-active{
    transition: opacity .3s;
  }
  .router-fade-enter,.router-fade-leave-active{
    opacity: 0;
  }
</style>



