//获取用户历史记录localStorage

export const getStore=name =>{
    if(!name) return;
    return window.localStorage.getItem(name);
}

//储存记录
export const setStore=(name,count)=>{
    if(!name) return;
    if(typeof count !=='string')
    {
        count=JSON.stringify(count);
    }
    window.localStorage.setItem(name,count);
}

//删除记录
export const removeStore=name =>{
    if(!name) return;
    window.localStorage.removeItem(name);
}

//获取style样式
export const getStyle=(element,attr,Numbermode='int')=>{
    let target;
    //scollTop获取方式没有她不属于的style而且只有document.body才有
    if(attr==='scrollTop')
    {
        target=element.scrollTop;
    }else if(element.currentStyle)
    {
        //IE下获取样式
        target=element.currentStyle[attr];
    }else{
        //火狐下
        target=document.defaultView.getComputedStyle(element,null)[attr];
    }
    return Numbermode=='float'?parseFloat(target) : parseInt(target);
}

//显示返回顶部按钮:开始,运动,结束 这三个过程调用函数看是否到达终点
export const showBack=callback=>{
    let requestFram;
    let oldScrollTop;
    //滚动条滚动触发
    document.addEventListener('scroll',()=>{
        showBackFun();
    },false);
    document.addEventListener('touchstart',()=>{
        showBackFun();
    },{passive:true});
    ////passive:true告诉浏览器不会对事件的默认事件说no 
    //如果不告诉浏览器80%都不知道会不会调用preventDefault默认事件这样就会拖慢速度
    // capture: false,once: false，passive: false,
    //{}三个参数第一个bool值默认false不允许事件冒泡,第二个事件只能执行一个当前还没实现第三个不阻止事件默认行为
    document.addEventListener('touchmove',()=>{
        showBackFun();
    },{passive:true});
    document.addEventListener('touchend',()=>{
        oldScrollTop=document.body.scrollTop;
        moveEnd();
    },{passive:true});
    const moveEnd=()=>{
        requestFram=()=>{
            //如果离开的时候获取的滚动条高度和滚动条滚动完了的高度不一致就同步防止错误
            if(document.body.scrollTop!=oldScrollTop)
            {
                oldScrollTop=document.body.scrollTop;
            }else{
                cancelAnimationFrame(requestFram);
                showBackFun();
            }
        }
    }
    //判读是否到达终点
    const showBackFun=()=>{
        //console.log(document.body.scrollTop>500);
        if(document.body.scrollTop>500)
        {
            callback(true);
        }else{
            callback(false);
        }
    }
}
/*
    element当前对象
    target运动的目标值
    duration运动时间
    mode运动方式
    callback回调函数
*/
export const animate=(element,target,duration=400,mode="ease-out",callback)=>{
    //进入的时候取消定时器
    clearInterval(element.timer);
    //参数不同的情况下
    if(duration instanceof Function)
    {
        callback=duration;
        duration=400;
    }
    if(duration instanceof String)
    {
        mode=duration;
        duration=400;
    }
    if(mode instanceof Function )
    {
        callback=mode;
        mode="ease-out"
    }
    const attrstyle=attr=>{
        if(attr=='opcity')
        {
            //Math.round()方法可把一个数字舍入为最接近的整数。
            return Math.round(getStyle(element,attr,'float')*100);
        }else{
            
            return getStyle(element,attr);
        }
    }
    //根据字体大小将rem转换为px运算
    const rootSize=parseFloat(document.documentElement.style.fontSize);
    const unit={};
    const initState={};
    //获取目标属性的样式值
    //返回一个所有元素为字符串的数组Object.keys
    //获取属性单位的初始化样式值
    Object.keys(target).forEach(attr=>{
        if(/[^\d^\.]/gi.test(target[attr]))
        {
            unit[attr]=target[attr].match(/[^\d^\.]+/gi)[0] || 'px';
        }else{
            unit[attr]='px';
        }
        initState[attr]=attrstyle(attr);
    });
    //去掉传入的后缀单位
    Object.keys(target).forEach(attr=>{
        if(unit[attr]=='rem')
        {
            //ceil() 方法执行的是向上取整计算，
            target[attr]=Math.ceil(parseInt(target[attr]*rootSize));
        }else{
            target[attr]=parseInt(target[attr]);
        }
    });
    let flag=true;//假设运动到达的了终点
    const remberSpeed={};//记录一个速度值再ease-in模式下用到
    element.timer=setInterval(()=>{
        Object.keys(target).forEach(attr=>{
            let iSpeed=0;//步长
            let status=false;//是否任须运动
            let iCurrent=attrstyle(attr) || 0;//当前元素性质
            let speedBase=0;//目标点需要减去的基础值，三种运动状态的值都不同
            let interValTime;//将目标分为多少步执行数值越大步数越大时间越长
            switch(mode)
            {
                case 'ease-out':
                //每次获取更新后的滚动条长度再计算每次运动多次
                speedBase=iCurrent;
                interValTime=duration*5/400;
                break;
                case 'linear':
                //固定的滚动条长度然后计算每次运动多少
                interValTime=initState[attr]*20/400;
                speedBase=initState[attr];
                break;
                case 'ease-in':
                //根据上一步运动的值计算运动多少
                let oldspeed=remberSpeed[attr] || 0;
                iSpeed=oldspeed+(target[attr]-initState[attr])/duration;
                remberSpeed[attr]=iSpeed;
                break;
                default:
                speedBase=iCurrent;
                interValTime=duration*5/400;
            }
            if(mode !=='ease-in')
            {
                //要到达的位置-取滚动跳的位置就可以到达指定位置/要分为多少步执行就可以算出每步要执行多少
                iSpeed=(target[attr]-speedBase)/interValTime;
                //ceil向上取整floor向下取整
                iSpeed=iSpeed>0 ?Math.ceil(iSpeed) : Math.floor(iSpeed);
            }
            //判断是否到达
            switch(mode)
            {
                case 'ease-out':
                //根据动态的滚动条长度判读是否到达指定地
                status=iCurrent!=target[attr];
                break;
                case 'linear':
                //根据动态的滚动条长度判滚动条长度是否小于每次减去的值小于的话到达指定地
                status=Math.abs(Math.abs(iCurrent)-Math.abs(target[attr])) > Math.abs(iSpeed);
                break;
                case 'ease-in':
                //根据动态的滚动条长度是否小于上一次的长度小于的话到达指定地
                status=Math.abs(Math.abs(iCurrent)-Math.abs(target[attr])) > Math.abs(iSpeed);
                break;
                default:
                status=iCurrent!=target[attr];
            }
            if(status)
            {
                flag=false;
                //opacity和scrollTop需要特殊处理
                if(attr=='opacity')
                {
                    //filter添加滤镜
                    element.style.filter="alpha(opacity:"+(iCurrent+iSpeed)+")";
                }else if(attr==='scrollTop')
                {
                    element.scrollTop=iCurrent+iSpeed;
                }else{
                    element.style[attr]=iCurrent+iSpeed+'px';
                }
            }else{
                flag=true;
            }
            if(flag)
            {
                //到达指定地取消定时器
                clearInterval(element.timer);
                //是否传入了回调函数
                if(callback)
                {
                    callback();
                }
            }
        })
    },20);
}


